body{
    margin: 0;
    padding: 0;
    font-family: 'Oleo Script Swash Caps', cursive;
    height: 100vh;
}
.Container{
    display: flex;
    justify-content: center;
    align-items: center;

}
form{
    display: flex;
    flex-direction: column;
}
.heading{
    display: flex;
    justify-content: center;
    align-items: center;
}
.button-container{
    display: flex;
    justify-content: center;
    
}

#btn{
margin-top: 2rem;
    width: 6rem;
    height: 3rem;
    background-color: blueviolet;
    color: white;
    border-radius: 10px;
    border: none;
    margin-right: 2rem;
    font-family: 'Uchen', serif;


}
.container2{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.textArea{
    border-radius: 20px;
    padding: 10px;
    box-shadow: 5px 5px 5px black;
    border: none;
    outline: none;
}

@media screen and (max-width:500px){
   .textArea{
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
   }
   .button-container{
   display: grid;
   grid-template-columns:100px 100px 100px ;
   grid-column-gap: 0;
   -webkit-column-gap: 0;
           column-gap: 0;
   
   }
   button{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
   }
}
